import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
Vue.use(Router)

export const constantRouterMap = [
    {
        path: '/',
        component: Home,
        hidden: true,
        redirect: '/home',
        name: 'Weekend',
        children: [{
            path: '/home',
            component: (resolve) => require(['@/components/Home'], resolve),
            name: '首页',
            meta: { title: '首页' }
        }]
    }
]

export default new Router({
  mode: 'hash',
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})
